// global

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    padding: 0px .5rem;
    font-family: 'Exo';
}

main {
    max-width: $window__maxwidth;
    font-size: $font__base-size;
    line-height: $font__line-height-body;
    text-align: center;
}

a {
    &:after {
        text-decoration: none;
    
    &:visited {
        text-decoration: none;
        color: $color__green-branding;
    }
    }
}

ul {
    margin: 0px;
    padding: 0px;
}

.content-area {
    display: flex;
    justify-content: center;
}

#page {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.site-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    max-width: 1080px;
    margin: 0rem .75rem;
}

.site-content:before {

    @include desktop {
        content: "\e977";
        font-family: "Inhabitant-icons";
        color: $color__green-branding;
        font-size: 125px;
        margin: 0px 4rem 0rem 1rem;
    }
}

.site-content:after {

    @include desktop {
        content: "\e978";
        font-family: "Inhabitant-icons";
        color: $color__green-branding;
        font-size: 125px;
        margin: 0px 1rem 0rem 4rem;
    }
}

.green-branding {
    color: $color__green-branding;
}

fieldset {
    border: 0px;
}

.entry-div {
    text-align: center;
}

.entry-content {
    text-align: center;

    p {
        font-size: 1rem;
    }
}

.quotations {
    display: none;

    @include desktop {
        display: initial;
    }
}

// header

header {
    display: flex;
    justify-content: center;
    width: 100%;
}

.logo {
    width: 100%;
    margin: 4rem 0rem 1rem 0rem;
    max-width: 500px;


    a {
        width: 100%;
    }

    img {
        width: 100%;
    }

    @include desktop {
        max-width: 700px;
    }
}

// footer

footer {

    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 4rem;

    @include tablet {

        flex-direction: row;
        justify-content: center;
    }

    @include desktop {
        font-size: 1.25rem;
    }
}

.footer-nav {

    @include branded-a;

    ul {
        display: flex;
        flex-direction: row;
        
        li {
            margin: 0px;
            padding: 0px 1rem;
            list-style-type: none;

            border-right: 1px solid #fff;

            &:last-child {
                border-right: 0px;
            }
        }
    }
}

.external-link {

    @include branded-a;
    text-align: center;
    
    p {
        padding: 0px 1rem;

        @include tablet {

            margin: 0px;
        }
    }
}

//about 

.page {

    h1 {
        margin-bottom: 0rem;
    }

    p{
        @include branded-a;
        text-align: center;
        font-weight: normal;

        img {
            width: 1rem;
        }
    }  
}

// archive

.cats {
    max-width: 100%;
    text-align: center;
    
    h2 {
        font-size: 1.5em;
        margin-top: 3rem;
    }

    ul {


        li {
            display: unset;
            list-style-type: none;
            line-height: 2rem;
            margin-right: 1rem;

            a {
                text-decoration: none;
                color: $color__green-branding;

                @include tablet {
                    font-size: 1.25rem;
                }

                @include desktop {
                    font-size: 1.5rem;
                }

            }
        }
    }
}  

// front-page

button {
    background-color: transparent;
    border: 2px solid #0c0;
    text-shadow: unset;
    box-shadow: unset;
    padding: .75rem 2.25rem;
    font-family: Exo, serif;
    font-weight: bold;
    font-size: 1rem;
    margin-top: 3rem;
    outline: none;

    &:focus {
        outline: none;
        border-color: initial;
        box-shadow: unset;
    }

    &:active {
        border-color: initial;
        box-shadow: unset;
    }

    @include tablet {
        font-size: 1.25rem;
    }
}

.quote-content p {
    font-size: 1.25rem;

    @include tablet {
        font-size: 1.75rem;
    }

    @include desktop {
        font-size: 2.5rem;
    }
}

article {
    font-size: 1.25rem;
}

.quote-title {
    text-align: center;
}

// submit a quote

.mandatory {
    color: red;
}

textarea {

    max-width: 370px;

    @include tablet {
        width: 100%;
        max-width: unset;
    }
}

//search

.search-results-title {
    border-bottom: 1px, white, dotted;
    padding-bottom: 1.5rem;
}

.search-form {

    input {
        border: 2px solid $color__green-branding;
        color: #fff;
        background-color: transparent;
        width: 80%;
        max-width: 240px;

        &:focus {
            color: inherit;
        }

        @include tablet {
            font-size: 1.25rem;
        }
    }

    button {
        border: 0px;
        padding: 0px 0px 0px .5rem;

        @include tablet {
            font-size: 1.25rem;
        }
    }
}